"use client";

import { useState } from 'react';
import useSWR from 'swr';
import { Check, Trash2, X, MessageSquare, ShieldAlert } from 'lucide-react';
import { Card, Button, Badge } from '@/components/ui';

const fetcher = (url: string) => fetch(url).then(res => res.json());

export default function GuestbookAdmin() {
    // Custom fetcher that fetches ALL messages for admin (need a new API param or separate route?)
    // For simplicity, let's just fetch from the same route but maybe we need to filter?
    // Actually, current public GET only returns approved. We need an admin GET.
    // Let's modify the GET route first or just use a client fetch for now that assumes admin session? 
    // Wait, the API GET route I wrote filters { approved: true }. I need to change that.

    // Changing approach: Let's assume I'll fix the API to return all if admin, or add ?all=true
    const { data: messages, mutate } = useSWR('/api/guestbook?admin=true', fetcher);

    const toggleApproval = async (id: string, currentStatus: boolean) => {
        await fetch(`/api/guestbook/${id}`, {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ approved: !currentStatus })
        });
        mutate();
    };

    const deleteMessage = async (id: string) => {
        if (!confirm('Delete this message?')) return;
        await fetch(`/api/guestbook/${id}`, { method: 'DELETE' });
        mutate();
    };

    if (!messages) return (
        <div className="flex flex-col items-center justify-center min-h-[400px] gap-4">
            <div className="w-12 h-12 border-4 border-accent/20 border-t-accent rounded-full animate-spin" />
            <p className="text-secondary font-mono animate-pulse">Scanning guestbook database...</p>
        </div>
    );

    return (
        <div className="space-y-8">
            <header>
                <h1 className="text-4xl font-display font-bold text-primary tracking-tight mb-2">Guestbook Moderation</h1>
                <p className="text-secondary text-lg">Approve or remove messages from the public guestbook.</p>
            </header>

            <Card padding="none" className="overflow-hidden bg-surface/30 backdrop-blur-sm">
                <div className="overflow-x-auto">
                    <table className="w-full text-left">
                        <thead>
                            <tr className="bg-surfaceHighlight/50 border-b border-border">
                                <th className="p-6 text-xs font-bold text-secondary uppercase tracking-widest">Signer</th>
                                <th className="p-6 text-xs font-bold text-secondary uppercase tracking-widest">Message</th>
                                <th className="p-6 text-xs font-bold text-secondary uppercase tracking-widest">Status</th>
                                <th className="p-6 text-xs font-bold text-secondary uppercase tracking-widest text-right">Actions</th>
                            </tr>
                        </thead>
                        <tbody className="divide-y divide-border">
                            {messages.map((msg: any) => (
                                <tr key={msg._id} className="hover:bg-surfaceHighlight/30 transition-colors group">
                                    <td className="p-6">
                                        <div className="flex items-center gap-4">
                                            <div className="w-10 h-10 rounded-full bg-surfaceHighlight flex items-center justify-center text-primary font-bold border border-border">
                                                {msg.name.charAt(0).toUpperCase()}
                                            </div>
                                            <div>
                                                <p className="text-primary font-bold">{msg.name}</p>
                                                <p className="text-xs text-secondary/50">{new Date(msg.createdAt).toLocaleDateString()}</p>
                                            </div>
                                        </div>
                                    </td>
                                    <td className="p-6">
                                        <p className="text-sm text-secondary leading-relaxed max-w-md italic">
                                            "{msg.message}"
                                        </p>
                                    </td>
                                    <td className="p-6">
                                        {msg.approved ? (
                                            <Badge variant="accent" className="bg-green-500/10 text-green-500 border-green-500/20">
                                                Live
                                            </Badge>
                                        ) : (
                                            <Badge variant="secondary" className="bg-yellow-500/10 text-yellow-500 border-yellow-500/20">
                                                Pending
                                            </Badge>
                                        )}
                                    </td>
                                    <td className="p-6">
                                        <div className="flex justify-end gap-2">
                                            <Button
                                                variant="ghost"
                                                size="sm"
                                                onClick={() => toggleApproval(msg._id, msg.approved)}
                                                className={`p-2 ${msg.approved ? 'text-yellow-500 hover:bg-yellow-500/10' : 'text-green-500 hover:bg-green-500/10'}`}
                                                title={msg.approved ? "Revoke Approval" : "Approve Message"}
                                            >
                                                {msg.approved ? <X size={18} /> : <Check size={18} />}
                                            </Button>
                                            <Button
                                                variant="ghost"
                                                size="sm"
                                                onClick={() => deleteMessage(msg._id)}
                                                className="p-2 text-red-500 hover:bg-red-500/10"
                                            >
                                                <Trash2 size={18} />
                                            </Button>
                                        </div>
                                    </td>
                                </tr>
                            ))}
                            {messages.length === 0 && (
                                <tr>
                                    <td colSpan={4} className="p-12 text-center">
                                        <div className="flex flex-col items-center gap-4 py-8">
                                            <MessageSquare size={48} className="text-secondary/20" />
                                            <p className="text-secondary text-lg italic">The guestbook is currently empty.</p>
                                        </div>
                                    </td>
                                </tr>
                            )}
                        </tbody>
                    </table>
                </div>
            </Card>
        </div>
    );
}
